body {
  display: flex;
  justify-content: center;
}

.main {
  text-align: center;
  border: 2px solid black;
  width: 60vw;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  padding: 1.2em;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.headers .btn {
  background-color: rgb(209, 209, 209);
  color: black;
  font-size: 1.1em;
  padding: 0.6em 1.2em;
  outline: none;
  border: 0;
  cursor: pointer;
}

.headers .active {
  background-color: rgb(101, 101, 101);
  color: white;
}

.addFields {
  margin: 2em 0;
  width: 44%;
  height: 30vh;
  display: flex;
  justify-content: space-around;
  align-items: space-around;
  flex-direction: column;
}

.addFields div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.5em 0;
  font-size: 1.1em;
  font-weight: bold;
}
.addFields div input {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  outline: none;
  width: 16em;
  font-size: 1.1em;
}

.loading {
  width: 10em;
}

.notFound {
  margin: 2em 0;
}

.card {
  background-color: rgb(219, 219, 219);
  box-shadow: 0 0 4px 2px rgb(201, 201, 201);
  margin: 1.6em 0 0 0;
  border: 3px solid rgb(2, 145, 145);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 20em;
  padding: 0.4em 1em;
  text-align: left;
  font-weight: bolder;
  font-size: 1.2em;
}

.card:nth-last-child(1) {
  margin-bottom: 1.6em;
}

.card .cardHeader {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5em;
}

.card .email {
  margin-bottom: 0.5em;
}

.card .address {
  font-size: 0.9em;
  font-weight: 500;
}
